nav{
    background-color: $color1;
    display: flex;
    justify-content: center;
    position: fixed;
    align-items: center;
    top: 0;
    margin: 0 auto;
    padding: .5em .5em;
    left: 0;
    right: 0;
    z-index: 99;

    // Menu List
    .nav_menu{
        align-items: center;
        font-size: 23px;
        color: $color5;
        display: flex;
        justify-content: center;
        gap: 8em;
        width: 50%;
        @include responsive(xl){
        width: 50%;
        }
        @include responsive(l){ 
        gap: 5em;
        }
        @include responsive(m){
        width: 70%;
        }
        @include responsive(s){
        // width: 90%;
        font-size: 18px;
        gap:3em;
        }
        @include responsive(xs){
            gap:2em;
        }

        &_list{
            list-style: none;

            // List
            &_link{
                text-decoration: none;
                color: $color6;
                &::after{
                content: '';
                display: block;
                width: 0;
                height: 7px;
                background: $color5;
                transition: all .1s ease-in;
                transform: translate(0px,-8px);
                opacity: 0;
                }
            }
        }
    }

    .nav_menu_list_link:hover::after {
        opacity: 1;
        width: 100%;
    }
    
    .active::after{
        content: '';
        display: block;
        width: 100%;
        height: 7px;
        background: $color5;
        transition: all .1s ease-in;
        transform: translate(0px,-8px);
        opacity: 1;
    }
    
    .aktif::after{
        content: '';
        display: block;
        width: 100%;
        height: 7px;
        background: $color5;
        transition: all .1s ease-in;
        transform: translate(0px,-18px);
        opacity: 1;
    }
    
    img{
        width: 40px;
        @include responsive(s){
            width: 35px;
        }
    }
}