.work{
    padding-top: 10em;
    display: flex;
    flex-direction: column;
    gap: 10em;
    align-items: center;
}

.container_work{
    display: flex;
    justify-content: center;
    align-items: center;
    width: 45%; 
    height:380px; 
    margin:0 auto;
    background: url(https://s3-us-west-2.amazonaws.com/s.cdpn.io/21555/KittE_a_Van_cat_kitten.jpg) no-repeat #e6e6e6; 
    background-size:100%;
    background-position:center center;
  -webkit-transition: all 2s cubic-bezier(0.000, 0.000, 0.580, 1.000); 
     -moz-transition: all 2s cubic-bezier(0.000, 0.000, 0.580, 1.000); 
      -ms-transition: all 2s cubic-bezier(0.000, 0.000, 0.580, 1.000); 
       -o-transition: all 2s cubic-bezier(0.000, 0.000, 0.580, 1.000); 
          transition: all 2s cubic-bezier(0.000, 0.000, 0.580, 1.000); /* ease-out */
  
  }
  .container_work:hover{
    background-size:125%;
  }

h2{
    text-align: center;
}
.other_work{
    // background-color: aqua;
    width: 50%;
    display: flex;
    flex-wrap: wrap;
    gap: 1em;
    .work{
        display: flex;
        justify-content: center;
        align-items: center;
        width: 300px; 
        height:130px; 
        margin:0 auto;
        background: url(https://s3-us-west-2.amazonaws.com/s.cdpn.io/21555/KittE_a_Van_cat_kitten.jpg) no-repeat #e6e6e6; 
        background-size:100%;
        background-position:center center;
    }
}