.me{
  padding-top: 10em;
  @include responsive(l){
  padding-top: 8em;
  }
}

// About
.about{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 2em;
    @include responsive(hd){
      gap: 0;
    }
    @include responsive(l){
      margin: 0 11em;
    }
    @include responsive(m){
      margin: 0 6em;
    }
    @include responsive(s){
      margin: 0 4em;
    }
    @include responsive(xs){
      margin: 0 2em;
    }

    &_img{
        display: flex;
        align-items: center;
        justify-content: center;
        img{
            width: 90%;
            border-radius: 1em;
            filter: grayscale(50%);
            @include responsive(hd){
              width: 80%;
            }
            @include responsive(xl){
              width: 75%;
            }
            @include responsive(l){
              width: 100%;
            }
        }
    }

    &_desc{
        word-spacing: 2px;
        display: flex;
        width: 500px;
        flex-direction: column;
        justify-content: flex-start;
        color: $color6;
        @include responsive(l){
          margin-top: 2em;
          width: 100%;
        }

        &_name{
            text-align: left;
            color: $color6;
            font-size: 60px;
            font-weight: 800;
            @include responsive(hd){
            font-size: 50px;
            }
            @include responsive(xs){
            font-size: 40px;
            }
            @include responsive(l){
            font-size: 50px;
            }
        }

        p{
            font-size: 20px;
            @include responsive(xl){
              font-size: 17px;
            }
            @include responsive(l){
              font-size: 20px;
            }
            @include responsive(xs){
            font-size: 17px;
            margin-top: -5px;
            }
        }

        .skill{
            display: flex;
            gap: 3em;
            ul{
              margin-top: -2px;
                li{
                    margin-top: 10px;
                    font-size: 20px;
                    list-style: none;
                    @include responsive(xl){
                      font-size: 17px;
                    }
                    @include responsive(l){
                      font-size: 20px;
                    }
                    @include responsive(xs){
                      font-size: 17px;
                    }
                }
            }
        }

    }
}

// HISTORY

.title_history{
  color: $color3;
  // background-color: violet;
  @include responsive(xs){
    text-align: center;
    padding: 0 1em;
    margin-top: 2em;
    font-size: 50px;
  }
}

.history {
    // background-color: yellow;
    margin-bottom: 2em;
    display: flex;
    gap: 3em;
    justify-content: center;
    // align-items: center;
    @include responsive(xs){
      margin: 0 2em;
      flex-direction: column;
    }
  }

.history_container{
  position: relative;
  width: 10%;
  height: 50vh;
  color: $color6;
  overflow: hidden;
  transition: width .8s ease;
  border-radius: 20px;
  
  //   &:first-child{
  //     margin-top: 15em;
  //   }
  //   &:nth-child(2){
  //       margin-top: 10em;
  //   }
  //   &:nth-child(3){
  //       margin-top: 5em;
  //   }
  @include responsive(xs){
    width: 50%;
    height: 8vh;
    margin: 2em 0 0 0;
      &:nth-child(2){
      margin: 0 0 0 3.5em;
      }
      &:nth-child(3){
      margin: 0 0 0 6.5em;
      }
      &:nth-child(4){
        margin: 0 0 0 9.5em;
        }
  }
    
    &:before, &:after {
      transform: translate3d(0, 0, 0);
      content: '';
      position: absolute;
      left: 0; top: 0;
      width: 100%; height: 100%;
    }
  
    &:after {
      background-color: $color2;
      transition: opacity 0.5s ease;
    }
  
    &:before {
      background: linear-gradient(to bottom, rgba(0,0,0,0) 0%,rgba(0,0,0,1) 750%);
      z-index: 1;
      opacity: 0;
      transform: translate3d(0, 0, 0) translateY(50%);
      transition: opacity 0.5s ease, transform 0.5s ease;
    }
  
    &:hover {
      width: 20% !important;

      .history_year{
        opacity: 0;
        transition: opacity .5s ease;
      }
      
      &:after {
        opacity: 0;
      }
  
      &:before {
        opacity: 1;
        transform: translate3d(0, 0, 0) translateY(0);
        transition: opacity 1s ease, transform 1s ease 0.25s;
      }
      
      .history_content {
        opacity: 1;
        transform: translateY(0);
        transition: all 0.75s ease 0.5s;
      }
  
      .tl-bg {
        filter: grayscale(0);
      }
    }
  }
  
  .history_content {
    transform: translate3d(0, 0, 0) translateY(25px);
    position: relative;
    z-index: 1;
    text-align: center;
    margin: 0 1.618em;
    top: 55%;
    opacity: 0;
    
    h1 {
      text-transform: uppercase;
      color: $color4;
      font-size: 1.44rem;
      font-weight: normal;
    }
  }
  
  .history_year {
    text-align: center;
    width: 100%;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translateX(-50%) translateY(-50%);
    z-index: 1;
    p {
      font-size: 15px;
    }
  }