.bg{
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
    height: 100vh;
}

.hero{
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    height: 60vh;
    padding-top: 12em;
    @include responsive(s){
        height: 50vh;
    }

    // Hi,Iam
    h1{
        font-size: 35px;
        color: $color6;
        font-weight: 200;
        @include responsive(hd){
            font-size: 30px;
        }
        @include responsive(s){
            font-size: 25px;
        }
        @include responsive(xs){
            font-size: 20px;
        }
    }

    // RIDWANNDWIP
    h2{
        margin-top: 5px;
        color: $color6;
        font-size: 70px;
        font-weight: 800;
        @include responsive(hd){
            font-size: 65px;
        }
        @include responsive(s){
            font-size: 60px;
        }
        @include responsive(xs){
            font-size: 35px;
        }
    }

    // TYPEWRITER
    h3{
        color: $color6;
        font-size: 35px;
        font-weight: 200;
        margin-top: 15px;
        @include responsive(hd){
            font-size: 30px;
        }
        @include responsive(s){
            font-size: 25px;
        }
        @include responsive(xs){
            font-size: 20px;
        }
    }

    // LINE
    &_line{
        margin: 1.5em;
        width: 450px;
        height: 1px;
        background-color: $color4;
        @include responsive(hd){
        width: 400px;
        }
        @include responsive(s){
        width: 350px;
        }
        @include responsive(xs){
        width: 300px;
        }
    }

    // SOCIAL
    &_social{
        ul{
            display: flex;
            gap: 3em;
            @include responsive(s){
            gap: 2em;
            }
            li{ 
                cursor: pointer;
                list-style: none;
                svg{
                    width: 30px;
                    height: 30px;
                    transition: all .1s ease-in;
                    fill: $color4;
                    &:hover{
                        fill: $color6;
                        transform: translateY(-5px);
                    }
                    @include responsive(m){
                    width: 20px;
                    height: 20px;
                    }
                }
                path{
                    stroke: 10px;
                }
            }
        }
    }
}